import React, { Component } from 'react';
import HamburgerMenu from 'react-hamburger-menu';
import { Link } from 'react-router-dom';
import Media from 'react-media';
import logo from '../../img/logo/LogoCCT.png';
import logoW from '../../img/logo/LogoCCT.webp';
import logoJ from '../../img/logo/LogoCCT.jp2';
import buttonsIMG from '../../img/elements/buttonsGroup.svg';
import style from './officeCompany.module.css';
import Navs from '../../Components/pagesComponents/startScreen/navs/navs';

export default class OfficeCompany extends Component {
  state = {
    open: false,
  };

  handleClick = () => {
    this.setState(prevState => ({ open: !prevState.open }));
  };

  render() {
    const { open } = this.state;
    return (
      <div className={style.wrapper}>
        <div className={style.navBar}>
          <div className={style.navBarNotTransparent}>
            <Link to="/">
              <picture>
                <source srcSet={logoW} type="image/webp" />
                <source srcSet={logoJ} type="image/jp2" />
                <source srcSet={logo} type="image/png" />
                <img className={style.img} src={logo} alt="Logo" />
              </picture>
            </Link>
            <div className={style.mediaMenu}>
              <Media query={{ maxWidth: 1200 }}>
                {matches =>
                  !matches && <span className={style.span}>МЕНЮ</span>
                }
              </Media>
              <HamburgerMenu
                isOpen={open}
                menuClicked={this.handleClick}
                color="#c0c0c2"
              />
            </div>
          </div>
        </div>
        <h2 className={style.header}>
          <Media query={{ maxWidth: 1200 }}>
            {matches =>
              !matches && (
                <img
                  className={style.buttons}
                  src={buttonsIMG}
                  alt="decorate"
                />
              )
            }
          </Media>
          личный кабинет для предприятий
        </h2>
        <p className={style.text}>Is in propgress</p>
        <Navs open={open} />
      </div>
    );
  }
}
