import React, { Component, lazy, Suspense } from 'react';
import { RotateSpinLoader } from 'react-css-loaders';
import style from './communalPortal.module.css';

const AsyncStartScreen = lazy(() =>
  import('../../Components/pagesComponents/startScreen/startScreen'),
);

const AsyncSecondScreen = lazy(() =>
  import('../../Components/pagesComponents/secondScreen/secondScreen'),
);

const AsyncThirdScreen = lazy(() =>
  import('../../Components/pagesComponents/thirdScreen/thirdScreen'),
);

const AsyncFourthScreen = lazy(() =>
  import('../../Components/pagesComponents/fourthScreen/fourthScreen'),
);

const AsyncFifthScreen = lazy(() =>
  import('../../Components/pagesComponents/fifthScreen/fifthScreen'),
);

const AsyncSixthScreen = lazy(() =>
  import('../../Components/pagesComponents/sixthScreen/sixthScreen'),
);

const AsyncSeventhScreen = lazy(() =>
  import('../../Components/pagesComponents/seventhScreen/seventhScreen'),
);

const AsyncEighthScreen = lazy(() =>
  import('../../Components/pagesComponents/eighthScreen/eighthScreen'),
);

const AsyncFooter = lazy(() =>
  import('../../Components/pagesComponents/footer/footer'),
);

const AsyncModal = lazy(() => import('../../Components/modalForm/modalForm'));

export default class CommunalPortal extends Component {
  state = {
    open: false,
  };

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  componentDidMount = () => {
    window.scrollTo(0, 0);
  };

  render() {
    const { open } = this.state;
    return (
      <div className={style.wrapper}>
        <Suspense fallback={<RotateSpinLoader />}>
          <AsyncStartScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncSecondScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncThirdScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncFourthScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncFifthScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncSixthScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncSeventhScreen />
        </Suspense>
        <Suspense fallback={<div />}>
          <AsyncEighthScreen />
        </Suspense>
        <button
          className={style.button}
          type="button"
          onClick={this.onOpenModal}
        >
          оставить заявку
        </button>
        <Suspense fallback={<div />}>
          <AsyncFooter />
        </Suspense>
        {open && (
          <Suspense fallback={<div />}>
            <AsyncModal open={open} onCloseModal={this.onCloseModal} />
          </Suspense>
        )}
      </div>
    );
  }
}
