import React, { Component } from 'react';
import posed from 'react-pose';
import { Link } from 'react-router-dom';
import items from './items';
import style from './navs.module.css';

const Sidebar = posed.div({
  open: {
    y: '0%',
    transition: { duration: 300 },
    delayChildren: 0,
    staggerChildren: 150,
  },
  closed: {
    y: '-100%',
    delay: 0,
    transition: { duration: 300 },
  },
  initialPose: 'closed',
});

const Item = posed.li({
  open: { opacity: 1 },
  closed: { opacity: 0 },
});

export default class Navs extends Component {
  state = {};

  onHandleClick = () =>
    this.setState(prevState => ({ subOpen: !prevState.subOpen }));

  render() {
    const { open } = this.props;
    return (
      <Sidebar className={style.navsWrapper} pose={open ? 'open' : 'closed'}>
        <div>
          <nav className={style.nav}>
            <ul className={style.navs}>
              {items.map(item => (
                <Item className={style.listItem} key={item.name}>
                  <Link
                    className={style.link}
                    to={item.href}
                    spy
                    smooth
                    duration={500}
                  >
                    {item.name}
                  </Link>
                </Item>
              ))}
            </ul>
          </nav>
          <div className={style.contacts}>
            <p>
              <i className="material-icons">call</i>
              <i className="material-icons">near_me</i>
              <a href="tel:+380632781584">+38 (063) 278-15-84</a>
            </p>
            <p>
              <i className="material-icons">local_post_office</i>
              <a href="mailto:ceo@cybercreation.team">ceo@cybercreation.team</a>
            </p>
          </div>
        </div>
      </Sidebar>
    );
  }
}
